body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: black;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace;
}

html {
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

html, body, #root {
  display: block;
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Fira Sans, Droid Sans, Helvetica Neue, sans-serif;
}
section.sailsteer {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  background: black;
}

section.sailsteer.centered {
  display: flex;
  flex: 1 1;
  justify-content: center;
  align-items: center;
}

section.sailsteer h3.notification {
  display: inline-block;
  margin: 0;
  padding: 0;
  font-size: 1.25em;
  font-weight: normal;
  color: orange;
  opacity: 0.5;
}

section.sailsteer div.widgets-left,
section.sailsteer div.widgets-right {
  display: flex;
  height: 100%;
  width: 17%;
  position: absolute;
  top: 0;
  flex-direction: column;
  flex-wrap: wrap;
  justify-content: space-evenly;
}

section.sailsteer div.widgets-left {
  left: 0;
}

section.sailsteer div.widgets-right {
  right: 0;
}

.widget {
  display: flex;
  width: 100%;
  height: 18%;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.widget.left {
  border-color: orange;
  border-width: 1px;
  border-right-style: dotted;
  border-bottom-style: dotted;
  border-bottom-right-radius: 10px;
}

.widget.right {
  border-color: orange;
  border-width: 1px;
  border-left-style: dotted;
  border-bottom-style: dotted;
  border-bottom-left-radius: 10px;
}

.widget h1 {
  display: block;
  width: 100%;
  text-align: center;
  color: white;
  font-weight: bold;
  font-size: 2em;
  line-height: 1;
  margin: 0;
  padding: 0;
}

.widget h2 {
  display: block;
  width: 100%;
  text-align: center;
  text-transform: lowercase;
  color: white;
  font-weight: normal;
  /* font-weight: 100; */
  font-size: 0.8em;
  line-height: 1;
  margin: 0;
  padding: 0;
  opacity: 0.5;
}

.widget.position h1 {
  /* font-family: 'SFMono-Regular', Consolas, 'Liberation Mono', Menlo, Courier, monospace; */
  font-size: 0.9em;
  line-height: 1.5;
}

.widget.line-height h1,
.widget.line-height h2 {
  line-height: 1.1
}

section.wind {
  width: 100%;
  height: 100%;
  display: block;
  position: relative;
  background: black;
}

section.grid {
  width: 100%;
  height: 100%;
  display: block;
}

section.grid .rows {
  width: 100%;
  height: 100%;
  display: block;
  padding: 5px;
}

section.grid div.rows div.row {
  width: 100%;
  height: 50%;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-evenly;
  align-content: space-evenly;
}

section.grid div.rows div.row.double {
  height: 100%;
}

section.grid div.rows div.row div.col {
  height: 100%;
  text-align: center;
  flex-grow: 1;
  padding: 5px;
  display: table;
  font-size: 175%;
}

section.grid div.rows div.row div.col.xs-6 {
  font-size: 250%;
}

section.grid div.rows div.row.double div.col {
  font-size: 300%;
}

section.grid div.rows div.row.double div.col.xs-12 {
  font-size: 500%;
}

section.grid div.rows div.row div.col.xs-12 {
  width: 100%;
}

section.grid div.rows div.row div.col.xs-6 {
  width: 50%;
}

section.grid div.rows div.row div.col.xs-4 {
  width: 30%;
}

section.grid div.rows div.row div.col div.widget {
  display: table-cell;
  height: 100%;
  border-radius: 5px;
  border: 1px dotted orange;
  color: white;
  vertical-align: middle;
}

.freeboard, .freeboard .freeboard--outer,  .freeboard .freeboard--outer iframe {
  width: 100vw;
  height: 100vh;
}

